const actions = {
  getTaxes: (page, search, filter) => ({
    type: 'GET_TAXES_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createTax: () => ({
    type: 'CREATE_TAX_REQUEST'
  }),
  storeTax: data => ({
    type: 'STORE_TAX_REQUEST',
    payload: { body: data }
  }),
  editTax: id_tax => ({
    type: 'EDIT_TAX_REQUEST',
    payload: { id_tax: id_tax }
  }),
  updateTax: (id_tax, data) => ({
    type: 'UPDATE_TAX_REQUEST',
    payload: {
      id_tax: id_tax,
      body: data
    }
  }),
  showTax: id_tax => ({
    type: 'SHOW_TAX_REQUEST',
    payload: { id_tax: id_tax }
  }),
  deactivateTax: id_tax => ({
    type: 'DEACTIVATE_TAX_REQUEST',
    payload: { id_tax }
  }),
  activateTax: id_tax => ({
    type: 'ACTIVATE_TAX_REQUEST',
    payload: { id_tax }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
