import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Redirect } from "react-router-dom";

import TaxUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/tax/tax/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/taxes";

class UpdateTax extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editTax } = this.props;
    const { id } = this.props.match.params;
    editTax(id);
  }

  handleSubmit = value => {
    const { updateTax } = this.props;
    const { id } = this.props.match.params;
    value.percentage = value.percentage / 100;
    updateTax(id, value);
  };

  componentDidUpdate() {
    // Display error notif
    if (this.props.tax.error) {
      notification("error", this.props.tax.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.tax.success) {
      this.props.history.replace(redirectTo);
    }
  }

  render() {
    // console.log(this.props);
    //const { id } = this.props.match.params;

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="taxes.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.tax.loadingEdit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
              <TaxUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                tax={this.props.tax}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    tax: state.tax
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateTax);
